.staticHeader{
    height: 150px;
    display: flex;
    align-items: center;
    background: #313131;
    background: -webkit-linear-gradient(to right,#313131,#c7ede6);
    background: linear-gradient(to right,#313131,#c7ede6);
    /* border-bottom: 7px solid #74a2d6; */
}

.staticHeader img {
    height: 100%;
    margin: 0 15px;

    padding-bottom: 10px;
    width: auto;
}

.staticHeader > p {
    font-family: Impact, sans-serif;
    color: whitesmoke;
    font-size:50px;
    margin-left: 0px;

    text-shadow:
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.neon-line {
    position: absolute;
    top: 150px;
    width: 100%; 
    height: 2px;      
    background-color: white;
    box-shadow: 
        0px 5px 10px gold,  
        0px 10px 20px gold,  
        0px 15px 40px gold,  
        0px 20px 80px gold;  
    z-index: 1;
  }

