.meme-input {
  display: flex;
  flex-direction: column;
  margin: 15px;
}

.meme-input-item {
  display: flex;
  flex-direction: column;
}

.meme-input-item>textarea,
input {
  width: 100%;
  height: 28px;
  min-height: 50px;
  padding-left: 10px;
  padding-top: 5px;
  font-size: 16px;
}

.meme-input-item>div>textarea,
input {
  width: 100%;
  height: 28px;
  min-height: 50px;
  padding-left: 10px;
  padding-top: 5px;
  font-size: 16px;
}

.margin-10 {
  margin-top: 10px;
  margin-bottom: 10px;
}

.display-flex {
  display: flex;
}

.line-0 {
  border: 2px solid gray;
  border-radius: 4px;
  font-size: 16px;
  /* width: 100%; */
}

.lines {
  border: 2px solid gray;
  border-radius: 4px;
  font-size: 16px;
  width: 95%;
}

.btn-delete {
  border: none;
  cursor: pointer;
  appearance: none;
  background-color: inherit;
  height: 4.5rem;
  width: 4.5rem;
}

.meme-input-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  cursor: pointer;
  margin-bottom: 10px;
}

.meme-input-btn>button {
  display: flex;
  flex-direction: row;
  width: 220px;
  height: 50px;
  letter-spacing: 0.1rem;
}

.meme-input-btn>button img {
  width: 2.5rem;
  height: 2.5rem;
  align-self: center;
}

.add-new-line {
  background-color: #2e9f90;
  padding: 0.7rem 1rem;
  color: #fff;
  letter-spacing: 2px;
  cursor: pointer;
  border-radius: 10px;
  border: 2px dashed #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  transition: .4s;
}

.add-new-line.save {
  margin-right: 1rem;
  border: none !important;
}

.add-new-line span:last-child {
  display: none;
}

.add-new-line:hover {
  transition: .4s;
  border: 2px dashed #00BFA6;
  background-color: #fff;
  color: #000;
}

.add-new-line:hover>img {
  transition: .4s;
  filter: none !important;
}

.add-new-line:active {
  background-color: #87dbd0;
}

.buttons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: space-between;
}

.buttons>.left {
  justify-content: flex-start;
  justify-content: space-between;
}


.download-btn {
  display: inline-block;
  border-radius: 7px;
  border: none;
  background: #62b1c5;
  color: white;
  font-family: inherit;
  text-align: center;
  font-size: 16px;
  /* box-shadow: 0px 14px 56px -11px #1875FF; */
  width: 11em;
  padding: 0.7rem 1.5rem;
  transition: all 0.4s;
  cursor: pointer;
}

.download-btn span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.4s;
}

.download-btn span:after {
  content: 'for free';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.7s;
}

.download-btn:hover span {
  padding-right: 3.55em;
  box-shadow: 0px 14px 56px -11px #1875FF;
}

.download-btn:hover span:after {
  opacity: 4;
  right: 0;
  box-shadow: 0px 14px 56px -11px #1875FF;
}

/* display custom meme area */
.display-meme {
  width: inherit;
}

.meme-box {
  position: relative;
  max-width: 40rem;
  margin: 0 auto;

  border: #313131;
  border-width: 2px;
  border-style: solid;
}

.meme-box>.meme-text {
  display: flex;
  flex-direction: row;
}

.meme-box>img {
  display: block;
  margin: 0;
  width: 100%;

}

/* Memes Text Position Templates */
.meme-box>.line-bottom {
  top: calc(90% - 10px);
  justify-content: center;
}

.meme-box>.line-top {
  top: 1;
  justify-content: center;
}

.meme-box>.line {
  display: table;
}

.mememe-box>.line-default {
  top: 50%;
  left: 50%;
}

/* END of Memes Text Position Templates */

.meme-flip {
  transform: scalex(-1);
}

.meme-flip-y {
  transform: scaley(-1);
}

/* Colors options: white and black*/
.color-white {
  color: white;
  text-shadow:
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.color-black {
  color: black;
  text-shadow:
    2px 2px 0 #ffffff,
    -2px -2px 0 #ffffff,
    2px -2px 0 #ffffff,
    -2px 2px 0 #ffffff,
    0 2px 0 #ffffff,
    2px 0 0 #ffffff,
    0 -2px 0 #ffffff,
    -2px 0 0 #ffffff,
    2px 2px 5px #ffffff;
}

.margin-10 {
  margin-top: 10px;
}

/* Memes Catalog */
.meme-catalog {
  height: 165px !important;
  width: relative;
  border: 2px solid #2d2d2d;
  -webkit-box-shadow: 0 0 5px #fff;
  box-shadow: 0 0 5px #fff;
  border-radius: 5px;
}

.meme-catalog>fieldset {
  display: flex;
  flex-direction: row !important;
  align-items: space-between !important;
}

.meme-catalog>.right {
  position: absolute;
  top: 489px;
  right: calc(10% - 90px);
  border-top-right-radius: 3px;
}

.meme-catalog>.left {
  position: absolute !important;
  border-top-left-radius: 3px;
}


